.app {
  width: 100vw;
}

.wrapper {
  width: 70%;
  margin: 0 auto;
  background-color: rgb(249, 255, 242);
  height: 50vh;
  border: 3px solid teal;
  margin-top: 80px;
}

.nav-bar {
  padding-right: 40px;
  width: 100%;
  background-color: teal;
  display: flex;
  height: 40px;
  justify-content: center;
  border-bottom: 4px solid darkgreen;
}

.link {
  margin-left: 20px;
  text-decoration: none;
  padding-top: 10px;
  color: white;
  letter-spacing: 1.2px;
  font-weight: 500;
  text-transform: uppercase;
}

.link:hover {
  color: lavender;
}

.active-link {
  border-bottom: 3px solid white;
  color: palegreen;
}

h2 {
  font-size: 64px;
  text-transform: uppercase;
  letter-spacing: 1.4px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  margin-left: 40px;
  margin-top: 80px;
  text-align: center;
  padding: 10px;
}

p {
  margin-left: 40px;
  font-size: 1.5rem;
}